.input-wrapper {
  display: flex;
  flex-direction: column;
  gap: 6px;
  width: 100%;
}

.input__label {
  font-size: 14px;
  font-weight: 600;
  color: #374151;
  display: flex;
  align-items: center;
  gap: 4px;
}

.input__required {
  color: #ef4444;
  font-size: 16px;
}

.input {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #d1d5db;
  border-radius: 8px;
  font-size: 16px;
  font-family: inherit;
  transition: all 0.2s ease-in-out;
  background-color: white;
}

.input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.input::placeholder {
  color: #9ca3af;
}

.input--error {
  border-color: #ef4444;
}

.input--error:focus {
  border-color: #ef4444;
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}

.input--disabled {
  background-color: #f9fafb;
  color: #6b7280;
  cursor: not-allowed;
  border-color: #e5e7eb;
}

.input--disabled::placeholder {
  color: #d1d5db;
}

.input__error {
  font-size: 14px;
  color: #ef4444;
  display: flex;
  align-items: center;
  gap: 4px;
  margin-top: 2px;
}

/* Hover states */
.input:hover:not(.input--disabled):not(:focus) {
  border-color: #9ca3af;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .input {
    font-size: 16px; /* Prevents zoom on iOS */
  }
}