.card {
  background-color: white;
  border-radius: 12px;
  transition: all 0.2s ease-in-out;
  width: 100%;
}

/* Variants */
.card--default {
  border: 1px solid #e5e7eb;
}

.card--elevated {
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.card--elevated:hover {
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  transform: translateY(-2px);
}

.card--outlined {
  border: 2px solid #d1d5db;
}

.card--outlined:hover {
  border-color: #9ca3af;
}

/* Padding variants */
.card--padding-none {
  padding: 0;
}

.card--padding-small {
  padding: 16px;
}

.card--padding-medium {
  padding: 24px;
}

.card--padding-large {
  padding: 32px;
}

/* Header */
.card__header {
  margin-bottom: 16px;
}

.card__title {
  font-size: 20px;
  font-weight: 700;
  color: #111827;
  margin: 0 0 8px 0;
  line-height: 1.4;
}

.card__subtitle {
  font-size: 14px;
  color: #6b7280;
  margin: 0;
  line-height: 1.5;
}

/* Content */
.card__content {
  width: 100%;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .card--padding-small {
    padding: 12px;
  }
  
  .card--padding-medium {
    padding: 16px;
  }
  
  .card--padding-large {
    padding: 20px;
  }
  
  .card__title {
    font-size: 18px;
  }
}