/* Dashboard Page Styles */
.dashboard-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 20px;
}

.dashboard-page__container {
  max-width: 1200px;
  margin: 0 auto;
}

/* Header Styles */
.dashboard-header {
  background: white;
  border-radius: 12px;
  padding: 24px;
  margin-bottom: 24px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

.dashboard-header__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dashboard-header__user {
  display: flex;
  align-items: center;
  gap: 16px;
}

.dashboard-header__avatar {
  width: 48px;
  height: 48px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 18px;
  font-weight: 600;
}

.dashboard-header__info h1 {
  margin: 0 0 4px 0;
  font-size: 24px;
  font-weight: 600;
  color: #111827;
}

.dashboard-header__subtitle {
  margin: 0;
  color: #6b7280;
  font-size: 14px;
}

.dashboard-header__logout {
  min-width: 100px;
}

/* Content Styles */
.dashboard-content {
  margin-top: 24px;
}

.dashboard-grid {
  display: grid;
  gap: 24px;
}

.dashboard-card--full-width {
  grid-column: 1 / -1;
}

/* Upload Form Styles */
.upload-form {
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.upload-form__error-banner {
  background: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
  padding: 12px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
}

.upload-form__field {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.upload-form__label {
  font-size: 14px;
  font-weight: 500;
  color: #374151;
  display: flex;
  align-items: center;
  gap: 4px;
}

.upload-form__required {
  color: #dc2626;
}

.upload-form__select {
  padding: 12px 16px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  font-size: 14px;
  background: white;
  transition: all 0.2s ease;
}

.upload-form__select:focus {
  outline: none;
  border-color: #667eea;
  box-shadow: 0 0 0 3px rgba(102, 126, 234, 0.1);
}

.upload-form__select--error {
  border-color: #dc2626;
}

.upload-form__error {
  color: #dc2626;
  font-size: 12px;
  margin-top: 4px;
}

.upload-form__file-input {
  border: 2px dashed #d1d5db;
  border-radius: 8px;
  padding: 24px;
  text-align: center;
  transition: all 0.2s ease;
  background: #f9fafb;
}

.upload-form__file-input:hover {
  border-color: #667eea;
  background: #f8fafc;
}

.upload-form__file {
  display: none;
}

.upload-form__file-info {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.upload-form__file-placeholder {
  color: #6b7280;
  font-size: 14px;
}

.upload-form__file-details {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.upload-form__file-name {
  font-weight: 500;
  color: #374151;
  font-size: 14px;
}

.upload-form__file-size {
  color: #6b7280;
  font-size: 12px;
}

.upload-form__progress {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.upload-form__progress-bar {
  width: 100%;
  height: 8px;
  background: #e5e7eb;
  border-radius: 4px;
  overflow: hidden;
}

.upload-form__progress-fill {
  height: 100%;
  background: linear-gradient(90deg, #667eea 0%, #764ba2 100%);
  transition: width 0.3s ease;
}

.upload-form__progress-text {
  font-size: 12px;
  color: #6b7280;
  text-align: center;
}

.upload-form__actions {
  display: flex;
  justify-content: center;
  margin-top: 8px;
}

.upload-form__submit {
  min-width: 200px;
}

.upload-form__footer {
  border-top: 1px solid #e5e7eb;
  padding-top: 16px;
  margin-top: 16px;
}

.upload-form__info {
  font-size: 12px;
  color: #6b7280;
  line-height: 1.5;
  margin: 0;
}



/* Responsive Design */
@media (max-width: 768px) {
  .dashboard-page {
    padding: 16px;
  }

  .dashboard-header__content {
    flex-direction: column;
    gap: 16px;
    text-align: center;
  }

  .dashboard-header__user {
    flex-direction: column;
    gap: 12px;
  }

  .upload-form__submit {
    width: 100%;
  }
}

